// 出庫一覧、入庫一覧のスタイル

.p-inventoryTransactions-table {
  width: 1800px;
}

th,
td {
  width: 300px;
  height: 100px;
  vertical-align: middle;
  padding: 0 15px;
  border: 1px solid #ccc;
}

.fixed01,
.fixed02 {
  position: sticky;
  top: 0;
  left: 0;
  color: #fff;
  background: #333;

  &:before {
    content: "";
    position: absolute;
    top: -1px;
    left: -1px;
    width: 100%;
    height: 100%;
    border: 1px solid #ccc;
  }
}

.fixed01 {
  z-index: 2;
}

.fixed02 {
  z-index: 1;
}